.profile__name {
  max-width: 294px;
  font-size: 42px;
  line-height: 48px;
  font-weight: 500;
  margin: 0 0 16px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 934px) {
  .profile__name {
    max-width: 189px;
    font-size: 27px;
    line-height: 33px;
    margin: 0 0 14px;
  }
}